import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './home/home.component'; // Add this
import { AtlasComponent } from './atlas/atlas.component'; // Add this
import { BlogsComponent } from './blogs/blogs.component'; // Add this

const routes: Routes = [
  { path: '', component: HomeComponent },              // Add this
  { path: 'atlas', component: AtlasComponent },           // Add this
  { path: 'blogs', component: BlogsComponent }           // Add this
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
