import { Component, OnInit } from '@angular/core';
import { IriserviceService } from '../iriservice.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.less']
})
export class HomeComponent implements OnInit {

  constructor(private iriservice: IriserviceService) { }

  pStyle : boolean = false;
  users : Object;
  usersList : any[];


  firstClick() {
    console.log('g');
    this.pStyle = !this.pStyle;
    this.iriservice.getUsers();
  }
  ngOnInit() {
    this.iriservice.getUsers().subscribe(data => {
      this.usersList = data[0].data;
      console.log(this.usersList);
    })
  }

}
